.card {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(0.36deg, rgba(28, 27, 27, 0.9) 5.23%, rgba(46, 46, 46, 0) 98.38%);
  }

  @media (max-width: $tablet-width) {
    width: 328px;
    height: auto;
  }
}

.card img {
  display: flex;
  flex-wrap: wrap;
  object-fit: contain;
  width: 100%;
  height: 232px;
  object-fit: contain;
}

.card__description {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  align-content: flex-end;
  width: 100%;
  height: 100%;
  padding-top: 17px;
  padding-left: 17px;
  padding-right: 19px;
  padding-bottom: 20px;
  z-index: 1;
}

.card__budge {
  position: absolute;
  top: 15px;
  right: 19px;
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  width: 67px;
  height: 27px;
  padding-top: 2px;
  padding-left: 17px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 17px;
  align-items: center;
  font-feature-settings: 'pnum'on, 'lnum'on;
  color: $text-white;
  border: 2px solid $basic-hover;
  border-radius: 25px;

  &::before {
    position: absolute;
    content: "";
    top: 3px;
    left: 12px;
    width: 16px;
    height: 16px;
    background-image: url("../img/icon-star.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &--active {
    display: flex;
  }
}

.card__description h2 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  align-items: flex-end;
  font-feature-settings: 'pnum'on, 'lnum'on;
  color: $basic-white;
}

.card span {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 7px;
  padding-left: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 19px;
  display: flex;
  align-items: center;
  font-feature-settings: 'pnum'on, 'lnum'on;
  color: $text-white;
  opacity: 0.8;

  &::before {
    position: absolute;
    content: "";
    top: 2px;
    left: 2px;
    width: 11px;
    height: 14px;
    background-image: url("../img/icon-pipka.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.card__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.card__item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-right: 10px;
  padding-left: 22px;
  padding-right: 12px;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 19px;
  display: flex;
  align-items: center;
  font-feature-settings: 'pnum'on, 'lnum'on;
  color: $text-white;
  opacity: 0.8;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    width: 1px;
    height: 100%;
    background-color: $basic-white;
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 2px;
    width: 13px;
    height: 16px;
    background-image: url("../img/icon-person.svg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &:last-child::after {
    display: none;
  }

  &--person {
    &::before {
      background-image: url("../img/icon-person.svg");
    }
  }

  &--puzzle {
    &::before {
      left: 0;
      width: 16px;
      height: 16px;
      background-image: url("../img/icon-puzzle.svg");
    }
  }
}
